import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import BtnSave from 'components/common/button/BtnSave';
import BtnCancel from 'components/common/button/BtnCancel';
import { Row, Card, Form, Input, Checkbox } from 'antd';
import { trainingTypes } from 'dump/data';

const handleInitialValue = initialValue => {
  const initialValueDefault = {
    sign: '',
    type: '',
    maxScores: '',
    sumScores: false,
    score: false,
    subScores: false,
    view: false,
  };
  return { ...initialValueDefault, ...initialValue };
};

@Form.create({ name: 'formTrainingType' })
class FormTrainingType extends Component {
  state = {
    trainingTypeId: false,
    initialValue: {},
  };

  componentDidMount = () => {
    const { match } = this.props;
    if (match.params.id) {
      this.setState({ trainingTypeId: match.params.id });
      this.getTrainingById(match.params.id);
    }
  };

  getTrainingById = sign => {
    const trainingMap = trainingTypes.filter(training => training.sign === sign)[0];
    const initialValue = handleInitialValue(trainingMap);
    this.setState({ initialValue });
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        console.log('Received values of form: ', values);
      }
    });
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { trainingTypeId } = this.state;
    console.log(trainingTypeId);
    return (
      <Card title={trainingTypeId ? 'Cập nhật loại rèn luyện' : 'Thêm loại rèn luyện'}>
        <Row>
          <Form {...formItemLayout} onSubmit={this.handleSubmit} className="login-form">
            <Form.Item hasFeedback label="Ký hiệu" {...formItemLayout}>
              {getFieldDecorator('sign', {
                initialValue: initialValue.sign,
                rules: [
                  {
                    required: true,
                    message: '',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Tên loại RL" hasFeedback>
              {getFieldDecorator('type', {
                initialValue: initialValue.type,
                rules: [
                  {
                    required: true,
                    message: '',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Điểm tối đa" hasFeedback>
              {getFieldDecorator('maxScores', {
                initialValue: initialValue.maxScores,
                rules: [
                  {
                    required: true,
                    message: '',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Tính tổng điểm">
              {getFieldDecorator('sumScores', {
                initialValue: initialValue.sumScores,
                valuePropName: 'checked',
              })(<Checkbox />)}{' '}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Điểm học tập">
              {getFieldDecorator('score', {
                initialValue: initialValue.score,
                valuePropName: 'checked',
              })(<Checkbox />)}{' '}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Hiển thị">
              {getFieldDecorator('view', {
                initialValue: initialValue.view,
                valuePropName: 'checked',
              })(<Checkbox />)}{' '}
            </Form.Item>
            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel href="#training/type" />
            </Form.Item>
          </Form>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormTrainingType {...props} />
  </Page>
));



